import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { AliasDetailsComponent } from '@aliases/pages/alias-details/alias-details.component';
import { AliasesIndexComponent } from '@aliases/pages/aliases-index/aliases-index.component';
import { AliasesResolver } from '@aliases/pages/aliases-index/services/aliases-resolver.service';

import { AliasResolver } from '@aliases/pages/alias-details/services/alias-resolver.service';
import { AliasValuesResolver } from '@aliases/pages/alias-details/services/alias-values-resolver.service';
import { ProjectResolver } from '@projects/services/project-resolver.service';

const routes: Routes = [
  {
    path: '',
    component: AliasesIndexComponent,
    resolve: {
      aliases: AliasesResolver,
      project: ProjectResolver,
    },
  },
  {
    path: ':alias_id',
    component: AliasDetailsComponent,
    resolve: {
      alias: AliasResolver,
      alias_values: AliasValuesResolver,
    },
  },
];

@NgModule({
  imports: [
    RouterModule.forChild(routes),
  ],
  exports: [RouterModule],
})
export class AliasesRoutingModule { }
