import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute, ActivatedRouteSnapshot } from '@angular/router';

import icCloudOff from '@iconify/icons-ic/twotone-cloud-off';
import icGroup from '@iconify/icons-ic/twotone-group';
import icMoreVert from '@iconify/icons-ic/twotone-more-vert';
import icPageView from '@iconify/icons-ic/twotone-pageview';
import icTimer from '@iconify/icons-ic/twotone-timer';

import { Breadcrumb } from '@vex/interfaces/breadcrumb.interface';
import { TableColumn } from '@vex/interfaces/table-column.interface';

import { AliasValueResource } from '@core/http/alias-value-resource.service';
import { Alias, AliasValue } from '@data/schema';
import { AliasValueCreateComponent } from './components/alias-value-create/alias-value-create.component';

@Component({
  selector: 'alias-details',
  templateUrl: './alias-details.component.html',
  styleUrls: ['./alias-details.component.scss'],
})
export class AliasDetailsComponent implements OnInit {
  alias: Alias;

  aliasValueTitle = 'Alias Values';
  aliasValueColumns: TableColumn<Request>[] = [
    { label: 'Checkbox', property: 'checkbox', type: 'checkbox', visible: true },
    { label: 'Value', property: 'value', type: 'text', visible: true },
  ];
  aliasValueData: AliasValue[];

  icGroup = icGroup;
  icPageView = icPageView;
  icCloudOff = icCloudOff;
  icTimer = icTimer;
  icMoreVert = icMoreVert;

  crumbs: Breadcrumb[] = [{
    name: 'Aliases',
    routerLink: ['/aliases'],
    queryParams: this.route.snapshot.queryParams,
  }];

  constructor(
    private dialog: MatDialog,
    private route: ActivatedRoute,
    private aliasValueResource: AliasValueResource
  ) { }

  ngOnInit() {
    const snapshot: ActivatedRouteSnapshot = this.route.snapshot;

    this.aliasValueData = snapshot.data.alias_values.map(aliasValue => {
      return new AliasValue(aliasValue);
    });

    this.alias = new Alias(snapshot.data.alias);

    this.crumbs.push({
      name: this.alias.name,
    });
  }

  openAliasValueCreateDialog($event) {
    const dialogRef = this.dialog.open(AliasValueCreateComponent, {
      width: '600px',
    });

    const onCreateSub = dialogRef.componentInstance.onCreate.subscribe(($event) => {
      this.createAliasValue($event.value);
    });

    dialogRef.afterClosed().subscribe(() => {
      onCreateSub.unsubscribe();
    });
  }

  createAliasValue(value: string) {
    const aliasValue = {value, alias_id: this.alias.id};

    this.aliasValueResource.create(aliasValue).subscribe(res => {
      this.aliasValueData = this.aliasValueData.concat(new AliasValue(aliasValue));
    });
  }

  deleteAliasValue(aliasValues: AliasValue[]) {
    aliasValues.forEach(aliasValue => {
      const aliasValueId: AliasValue['id'] = aliasValue.id;

      this.aliasValueResource.destroy(aliasValueId).subscribe(res => {
        this.aliasValueData = this.aliasValueData.filter((candidate: AliasValue) => {
          return candidate.id !== aliasValueId;
        });
      });
    });
  }
}
