import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve } from '@angular/router';

import { AliasResource } from '@core/http/alias-resource.service';

@Injectable({
  providedIn: 'root',
})
export class AliasesResolver implements Resolve<any> {

  constructor(
    private aliasResource: AliasResource
  ) { }

  resolve(route: ActivatedRouteSnapshot) {
    return this.aliasResource.index({
      project_id: route.queryParams.project_id,
    });
  }
}
