import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve } from '@angular/router';

import { AliasValueResource } from '@core/http/alias-value-resource.service';

@Injectable({
  providedIn: 'root',
})
export class AliasValuesResolver {
  constructor(
    private aliasValueResource: AliasValueResource
  ) { }

  resolve(route: ActivatedRouteSnapshot) {
    return this.aliasValueResource.index(
      {project_id: route.queryParams.project_id, alias_id: route.params.alias_id}
    );
  }
}
