import { Injectable } from '@angular/core';

import { BehaviorSubject } from 'rxjs';

import { Alias } from '@data/schema/alias';

@Injectable({
  providedIn: 'root',
})
export class AliasesDataService {
  private subject = new BehaviorSubject(null);
  aliases$ = this.subject.asObservable();
  aliases: Alias[];

  constructor() { }

  set(aliases: Alias[]) {
    this.aliases = aliases;
    this.subject.next(aliases);
  }

  add(alias: Alias) {
    this.aliases.unshift(new Alias(alias));
    this.set(this.aliases);
  }

  delete(id: number) {
    this.aliases.splice(
      this.aliases.findIndex((existingAlias) => {
        return existingAlias.id === id;
      }), 1);

    this.set(this.aliases);
  }
}
