import { Component, EventEmitter, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

import icClose from '@iconify/icons-ic/twotone-close';
import icPerson from '@iconify/icons-ic/twotone-person';

@Component({
  selector: 'alias-value-create',
  templateUrl: './alias-value-create.component.html',
  styleUrls: ['./alias-value-create.component.scss'],
})
export class AliasValueCreateComponent implements OnInit {

  onCreate = new EventEmitter<object>();

  form: FormGroup;

  icClose = icClose;
  icPerson = icPerson;

  constructor(@Inject(MAT_DIALOG_DATA) public data: any,
              private dialogRef: MatDialogRef<AliasValueCreateComponent>,
              private fb: FormBuilder) { }

  ngOnInit() {
    this.form = this.fb.group({
      value: null,
    });
  }

  create() {
    const form = this.form.value;

    this.onCreate.emit(form);
    this.dialogRef.close();
  }
}
