import { SelectionModel } from '@angular/cdk/collections';
import { Location } from '@angular/common';
import { AfterViewInit, Component, Input, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { FormControl } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { MatFormFieldDefaultOptions, MAT_FORM_FIELD_DEFAULT_OPTIONS } from '@angular/material/form-field';
import { MatPaginator } from '@angular/material/paginator';
import { MatSelectChange } from '@angular/material/select';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { ActivatedRoute, ActivatedRouteSnapshot, Router } from '@angular/router';

import { untilDestroyed } from 'ngx-take-until-destroy';
import { filter } from 'rxjs/operators';

import { fadeInUp400ms, stagger40ms } from '@vex/animations';
import { TableColumn } from '@vex/interfaces/table-column.interface';

import { AliasesCreateComponent } from '@aliases/components/aliases-create/aliases-create.component';
import { AliasResource } from '@core/http/alias-resource.service';
import { FileService } from '@core/utils/file.service';
import { Alias, Project } from '@data/schema';

import { aioTableData, aioTableLabels } from './mock/aliases-index-data';
import { AliasesDataService, AliasesIndexIcons } from './services';

@Component({
  selector: 'aliases-index',
  templateUrl: './aliases-index.component.html',
  styleUrls: ['./aliases-index.component.scss'],
  animations: [
    fadeInUp400ms,
    stagger40ms,
  ],
  providers: [
    {
      provide: MAT_FORM_FIELD_DEFAULT_OPTIONS,
      useValue: {
        appearance: 'standard',
      } as MatFormFieldDefaultOptions,
    },
  ],
})
export class AliasesIndexComponent implements OnInit, AfterViewInit, OnDestroy {

  layoutCtrl = new FormControl('boxed');

  // Table settings
  @Input()
  columns: TableColumn<Alias>[] = [
    { label: 'Checkbox', property: 'checkbox', type: 'checkbox', visible: true },
    { label: 'Name', property: 'name', type: 'text', visible: true},
    { label: 'Components', property: 'components', type: 'button', visible: true },
  ];
  pageIndex = this.route.snapshot.queryParams.page || 0;
  pageSize = 10;
  pageSizeOptions: number[] = [5, 10, 20, 50];
  dataSource: MatTableDataSource<Alias> | null;
  selection = new SelectionModel<Alias>(true, []);
  searchCtrl = new FormControl();
  labels = aioTableLabels;

  aliasCategoryNames;
  project: Project;

  // Breadcrumb settings
  crumbs = [];

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  constructor(
    public icons: AliasesIndexIcons,
    private aliasesDataService: AliasesDataService,
    private aliasResource: AliasResource,
    private activatedRoute: ActivatedRoute,
    private dialog: MatDialog,
    private location: Location,
    private file: FileService,
    private route: ActivatedRoute,
    private router: Router
  ) { }

  get visibleColumns() {
    return this.columns.filter(column => column.visible).map(column => column.property);
  }

  ngOnInit() {
    this.aliasesDataService.set(this.route.snapshot.data.aliases);

    this.dataSource = new MatTableDataSource();
    this.aliasesDataService.aliases$.pipe(
      filter<Alias[]>(Boolean)
    ).subscribe(aliases => {
      this.dataSource.data = aliases;
    });

    this.searchCtrl.valueChanges.pipe(
      untilDestroyed(this)
    ).subscribe(value => this.onFilterChange(value));

    this.project = this.route.snapshot.data.project;

    this.crumbs.push({ name: this.project.name });
    this.crumbs.push({ name: 'Aliases' });
  }

  ngAfterViewInit() {
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }

  getAliases(params) {
    this.aliasResource.index(params).subscribe(
      (res: Alias[]) => {
        this.aliasesDataService.set(res);
      },
      error => {

      }
    );
  }

  showAlias(alias: Alias) {
    const path: string = this.file.join('/aliases', alias.id);
    const snapshot: ActivatedRouteSnapshot = this.route.snapshot;

    this.router.navigate([path], {
       queryParams: { project_id: snapshot.queryParams.project_id },
    });
  }

  deleteAlias(alias: Alias) {
    const snapshot: ActivatedRouteSnapshot = this.route.snapshot;
    const queryParams = { project_id: snapshot.queryParams.project_id };

    this.aliasResource.destroy(alias.id, queryParams).subscribe(res => {
      this.aliasesDataService.delete(alias.id);
      this.selection.deselect(alias);
    });
  }

  deleteAliases(aliases: Alias[]) {
    aliases.forEach(r => this.deleteAlias(r));
  }

  updateAlias(alias: Alias, params: object) {
    this.aliasResource.update(alias.id, params).subscribe(res => {
      for (const key in params) {
        if (params.hasOwnProperty(key)) {
          alias[key] = params[key];
        }
      }
    });
  }

  // openCreateDialog() {
  //   let dialogRef = this.dialog.open(AliasesCreateComponent, {
  //     width: '600px'
  //   });
  //
  //   const onCreateSub = dialogRef.componentInstance.onCreate.subscribe(($event) => {
  //     this.createAlias($event)
  //   });
  //
  //   dialogRef.afterClosed().subscribe(() => {
  //     onCreateSub.unsubscribe();
  //   });
  // }

  // createAlias(alias: Alias) {
  //   const snapshot: ActivatedRouteSnapshot = this.route.snapshot
  //   alias.project_id = snapshot.queryParams.project_id
  //
  //   this.aliasResource.create(alias).subscribe(
  //     (res: Alias) => {
  //       this.dataSource.data.push(res)
  //     },
  //     err => {
  //
  //     })
  // }

  onFilterChange(value: string) {
    if (!this.dataSource) {
      return;
    }
    value = value.trim();
    value = value.toLowerCase();
    this.dataSource.filter = value;
  }

  toggleColumnVisibility(column, event) {
    event.stopPropagation();
    event.stopImmediatePropagation();
    column.visible = !column.visible;
  }

  /** Whether the number of selected elements matches the total number of rows. */
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.data.length;
    return numSelected === numRows;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {
    this.isAllSelected() ?
      this.selection.clear() :
      this.dataSource.data.forEach(row => this.selection.select(row));
  }

  trackByProperty<T>(index: number, column: TableColumn<T>) {
    return column.property;
  }

  onComponentChange(change: MatSelectChange, row: Alias) {
    const aliases = this.aliasesDataService.aliases;
    const index = aliases.findIndex(c => c === row);
    aliases[index].components = change.value;
    this.aliasesDataService.set(aliases);
  }

  /**
   * On page change, add queryParam 'page' to URL
   */
  onPaginateChange($event) {
    const queryParams = Object.assign({}, this.route.snapshot.queryParams);
    queryParams.page = $event.pageIndex;

    const url = this
        .router
        .createUrlTree([], {relativeTo: this.activatedRoute, queryParams})
        .toString();

    this.location.go(url);
  }

  ngOnDestroy() { }
}
